<template>
  <div style="height: 100%">
    <div class="container">
      <el-header>
        <c-header class="header" />
      </el-header>
      <el-container class="main-container">
        <el-aside
          :style="{width: mode === 'edit' ? '0' : '240px'}"
          class="stage__aside"
        >
          <aside-menu />
        </el-aside>
        <el-main class="main__stage">
          <div class="stage__sketchpad">
            <operate />
            <sketchpad />
          </div>
          <div class="stage__code">
            <code-view />
          </div>
          <div :class="['stage__detail', mode === 'edit' ? 'width-0' : '']">
            <component-detail />
          </div>
        </el-main>
      </el-container>
    </div>
  </div>
</template>

<script>
import header from '@/components/header'
import asideMenu from '@/components/asideMenu'
import operate from '@/components/operate'
import sketchpad from '@/components/sketchpad'
import codeView from '@/components/codeView'
import componentDetail from '@/components/componentDetail'
export default {
  components: {
    asideMenu,
    'c-header': header,
    operate,
    sketchpad,
    codeView,
    componentDetail
  },
  computed: {
    mode () {
      return this.$store.state.mode
    }
  }
}
</script>

<style lang="scss" scoped>
  @import "@/assets/scss/index.scss";
  .container {
    height: 100%;
    display: flex;
    flex-direction: column;
  }

  .main-container {
    padding: 10px;
    background: #e7e7e7;
    .stage__aside {
      width: 240px;
      transition: width 300ms;
    }
    .main__stage {
      margin-left: 10px;
      text-align: left;
      width: 100%;
      height: 100%;
      padding: 0;
      display: flex;
      .stage__sketchpad {
        padding: 10px;
        min-width: 360px;
        background: #fff;
        flex: 1;
        overflow-y: scroll;
      }
      .stage__code {
        min-width: 360px;
        margin-left: 4px;
        background: #23241f;
        flex: 1;
        overflow-y: scroll;
      }
      .stage__detail {
        background: #fff;
        margin-left: 10px;
        width: 400px;
        overflow-y: scroll;
        transition: width 300ms;
        opacity: 1;
      }
      .width-0 {
        width: 0;
        margin-left: 0;
        opacity: 0;
      }
    }
  }
</style>
